"use client";

import Link from "next/link";
import InteractiveNavigation from "@/components/client/InteractiveNavigation";
import Sidebar from "@/components/Sidebar";
import { useEffect, useState } from "react";
import { supabase } from "@/lib/supabaseClient";
import { usePathname } from "next/navigation";

export default function Navigation() {
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [userRole, setUserRole] = useState("user");
  const [authChecked, setAuthChecked] = useState(false);
  const pathname = usePathname();

  // Pages where navigation should be hidden
  const HIDDEN_NAVIGATION_PATHS = [
    "/login",
    "/register",
    "/forgot-password",
    "/admin",
    // Add more paths as needed
  ];

  // Check if navigation should be hidden
  const shouldHideNavigation = HIDDEN_NAVIGATION_PATHS.some((path) =>
    pathname.startsWith(path)
  );

  useEffect(() => {
    supabase.auth.getUser().then(({ data }) => {
      setIsAuthenticated(!!data.user);
      setAuthChecked(true);
    });
    const { data: listener } = supabase.auth.onAuthStateChange(
      (_event, session) => {
        setIsAuthenticated(!!session?.user);
        setAuthChecked(true);
      }
    );
    return () => {
      listener?.subscription.unsubscribe();
    };
  }, []);

  const publicLinks = isAuthenticated ? [] : [];

  const userLinks = isAuthenticated
    ? [
        { href: "/home", label: "Home" },
        { href: "/dashboard", label: "Dashboard" },
        { href: "/calendar", label: "Calendar" },
      ]
    : [];

  const adminLinks = [{ href: "/admin", label: "Admin Panel" }];

  if (!authChecked || shouldHideNavigation) {
    // Optionally, render a skeleton or nothing for fast tab switching
    return null;
  }

  return (
    <>
      <nav className="bg-white/95 backdrop-blur-sm border-b border-slate-200 sticky top-0 z-50">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center h-16">
            <InteractiveNavigation
              isAuthenticated={isAuthenticated}
              userRole={userRole}
              publicLinks={publicLinks}
              userLinks={userLinks}
              adminLinks={adminLinks}
            />
          </div>
        </div>
      </nav>
      
      {/* Sidebar for authenticated users */}
      <Sidebar
        isAuthenticated={isAuthenticated}
        userRole={userRole}
        userLinks={userLinks}
        adminLinks={adminLinks}
      />
    </>
  );
}
