"use client";

import Link from "next/link";
import { usePathname } from "next/navigation";
import { Button } from "@/components/ui/button";
import { User, LogOut, ChevronDown, Settings } from "lucide-react";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import { supabase } from "@/lib/supabaseClient";

interface InteractiveNavigationProps {
  isAuthenticated: boolean;
  userRole: string;
  publicLinks: Array<{ href: string; label: string }>;
  userLinks: Array<{ href: string; label: string }>;
  adminLinks: Array<{ href: string; label: string }>;
}

export default function InteractiveNavigation({
  isAuthenticated,
  userRole,
  publicLinks,
  userLinks,
  adminLinks,
}: InteractiveNavigationProps) {
  const pathname = usePathname();

  const allLinks = [
    ...publicLinks,
    ...(isAuthenticated ? userLinks : []),
    ...(isAuthenticated && userRole === "admin" ? adminLinks : []),
  ];

  const isActiveLink = (href: string) => {
    return pathname === href;
  };

  return (
    <>
      {/* Mobile/Tablet Layout */}
      <div className="md:hidden flex items-center justify-between w-full">
        {/* Left: Logo + Website Name */}
        <Link
          href="/"
          className="flex items-center space-x-2 hover:opacity-80 transition-opacity"
        >
          <img
            src="/icons/icon-192x192.png"
            alt="Optifi Logo"
            className="h-8 w-8"
          />
          <div className="text-xl font-bold accent-text">Optifi</div>
        </Link>

        {/* Right: Sign In Button (for non-authenticated users only) */}
        {!isAuthenticated && (
          <div className="flex items-center space-x-3">
            <Link href="/login">
              <Button
                variant="ghost"
                className="text-slate-700 hover:text-slate-900 text-base font-bold"
              >
                Sign In
              </Button>
            </Link>
          </div>
        )}
      </div>

      {/* Desktop Layout (Logo + User Toggle Only) */}
      <div className="hidden md:flex items-center justify-between w-full">
        {/* Left: Logo + Website Name */}
        <Link
          href="/"
          className="flex items-center space-x-2 hover:opacity-80 transition-opacity"
        >
          <img
            src="/icons/icon-192x192.png"
            alt="Optifi Logo"
            className="h-8 w-8"
          />
          <div className="text-xl font-bold accent-text">Optifi</div>
        </Link>

        {/* Right: Profile Toggle */}
        <div className="flex items-center">
          {isAuthenticated ? (
            <DropdownMenu>
              <DropdownMenuTrigger asChild>
                <Button
                  variant="ghost"
                  className="flex items-center space-x-2 px-3 py-2 rounded-lg hover:bg-slate-100"
                >
                  <div className="w-8 h-8 bg-gradient-to-br from-blue-500 to-purple-600 rounded-full flex items-center justify-center">
                    <User className="h-4 w-4 text-white" />
                  </div>
                  <span className="text-sm font-medium text-slate-700">
                    Account
                  </span>
                  <ChevronDown className="h-4 w-4 text-slate-500" />
                </Button>
              </DropdownMenuTrigger>
              <DropdownMenuContent className="w-56" align="end">
                <div className="px-3 py-2 border-b border-slate-100">
                  <p className="text-sm font-medium text-slate-900">
                    User Account
                  </p>
                  <p className="text-xs text-slate-500">Manage your account</p>
                </div>
                <DropdownMenuSeparator />
                <DropdownMenuItem asChild>
                  <Link href="/profile">
                    <User className="mr-2 h-4 w-4" />
                    <span>Profile</span>
                  </Link>
                </DropdownMenuItem>
                <DropdownMenuItem asChild>
                  <Link href="/settings">
                    <Settings className="mr-2 h-4 w-4" />
                    <span>Settings</span>
                  </Link>
                </DropdownMenuItem>
                <DropdownMenuSeparator />
                <DropdownMenuItem
                  onClick={async () => {
                    await supabase.auth.signOut();
                    window.location.href = "/";
                  }}
                  className="text-red-600 focus:text-red-700"
                >
                  <LogOut className="mr-2 h-4 w-4" />
                  <span>Log out</span>
                </DropdownMenuItem>
              </DropdownMenuContent>
            </DropdownMenu>
          ) : (
            <div className="flex items-center space-x-3">
              <Link href="/login">
                <Button
                  variant="ghost"
                  className="text-slate-700 hover:text-slate-900 text-base font"
                >
                  Sign In
                </Button>
              </Link>
            </div>
          )}
        </div>
      </div>
    </>
  );
}
